import { env } from "configs/EnvironmentConfig";
import { NAVIGATION_ASSETS_RIGHTS } from "constants/CommonConstants";
import { useCallback } from "react";
import { useSelector } from "react-redux";

const useAuthRights = () => {
  const userInfo = useSelector(state => state.myTeams.userInfo);

  const hasAssetsRights = useCallback(
    (assetId, assetRight = NAVIGATION_ASSETS_RIGHTS.VIEW) => {
      if (env.IS_DEV_MODE) {
        return true;
      }
      if (userInfo?.roleAssetPrivilegeVO) {
        const _assetRights = userInfo.roleAssetPrivilegeVO.assets.find(_asset => _asset.asset.assetId === assetId);
        if (_assetRights) {
          return _assetRights.privileges.find(right => right === assetRight);
        }
        return false;
      }
      return false;
    },
    [userInfo],
  );

  return { hasAssetsRights };
};

export default useAuthRights;
