import { notification } from "antd";
import axios from "axios";
import { API_BASE_URL } from "configs/AppConfig";
import { AUTH_TOKEN } from "redux/constants/Auth";
import history from "../history";

const AxiosInstance = axios.create({
  baseURL: API_BASE_URL,
  timeout: 60000,
});

// Config
export const ENTRY_ROUTE = "/auth/login";
const TOKEN_PAYLOAD_KEY = "Authorization";
const PUBLIC_REQUEST_KEY = "public-request";
//const SESSION_KEY = "JSESSIONID";
//const XSRF_KEY = "XSRF-TOKEN";

const onInterceptorRequestFulfilled = config => {
  const jwtToken = localStorage.getItem(AUTH_TOKEN);

  if (jwtToken) {
    config.headers[TOKEN_PAYLOAD_KEY] = jwtToken;
  }

  if (!jwtToken && !config.headers[PUBLIC_REQUEST_KEY]) {
    history.push(ENTRY_ROUTE);
    window.location.reload();
  }

  return config;
};

const onInterceptorRequestRejected = error => {
  notification.error({
    message: "Error",
  });
  Promise.reject(error);
};

const onInterceptorResponseRejected = async (error, oktaAuth) => {
  const notificationParam = {
    message: "",
  };

  if (error.response) {
    // Remove token and redirect
    if ([401, 403].includes(error.response.status)) {
      notificationParam.message = "Authentication Fail";
      notificationParam.description = "Please login again";
      localStorage.removeItem(AUTH_TOKEN);
      await oktaAuth.signOut("/");
    }
    if (error?.response?.data?.description || error?.response?.data?.message) {
      notificationParam.message = error?.response?.data?.message || error.response.data.description;
    } else {
      if (error.response.status === 400) {
        notificationParam.message = "Bad Request";
      } else if (error.response.status === 404) {
        notificationParam.message = "Not Found";
      } else if (error.response.status === 500) {
        notificationParam.message = "Internal Server Error";
      } else if (error.response.status === 508) {
        notificationParam.message = "Time Out";
      }
    }
  } else {
    notificationParam.message = error.message ?? "Unable to connect to server";
  }

  notification.error(notificationParam);

  return Promise.reject(error);
};

// AxiosInstance.interceptors.request.use(onInterceptorRequestFulfilled, onInterceptorRequestRejected);

// API respone interceptor
// AxiosInstance.interceptors.response.use(response => response.data, onInterceptorResponseRejected);

export default AxiosInstance;
export { onInterceptorRequestFulfilled, onInterceptorResponseRejected, onInterceptorRequestRejected };
