import { API_BASE_URL } from "configs/AppConfig";
import { notification } from "antd";
import { message } from "antd";
import { AUTH_TOKEN } from "redux/constants/Auth";
let responseInstance = {
  options: {
    apiUrl: API_BASE_URL,
    token: localStorage.getItem(AUTH_TOKEN),
  },
  lastStatus: {
    ok: false,
    status: 0,
    statusText: "",
    response: null,
  },
};
const processResponse = async resp => {
  // Copy response to lastStatus properties
  responseInstance.lastStatus.ok = resp.ok;
  responseInstance.lastStatus.status = resp.status;
  responseInstance.lastStatus.statusText = resp.statusText;
  responseInstance.lastStatus.url = resp.url;

  if (responseInstance.lastStatus.ok) {
    return resp;
  } else {
    return resp.text();
  }
};
const parsedString = msg => {
  const msgToDisplay = JSON.parse(msg);
  const parseMessage = JSON.parse(msgToDisplay);
  return parseMessage.message;
};
const handleError = lastStatus => {
  let msg = "";
  switch (lastStatus.status) {
    case 400:
      msg = JSON.stringify(lastStatus.response);
      notification.error({ message: parsedString(msg) });
      break;
    case 404:
      if (lastStatus.response) {
        msg = lastStatus.response;
        notification.error({ message: parsedString(msg) });
      } else {
        msg = `${lastStatus.statusText}
                 - ${lastStatus.url}`;
        notification.error({ message: parsedString(msg) });
      }
      break;
    case 500:
      msg = JSON.parse(lastStatus.response).message;
      notification.error({ message: parsedString(msg) });
      break;
    case 502:
      msg = JSON.parse(lastStatus.response).message;
      notification.error({ message: parsedString(msg) });
      break;
    default:
      msg = JSON.stringify(lastStatus);
      notification.error({ message: parsedString(msg) });
      break;
  }
  if (msg) {
    console.error(msg);
  }

  return msg;
};

const get = async (apiConstant, file) => {
  const { path, method } = apiConstant;
  let res;
  return await fetch(responseInstance.options.apiUrl + path, {
    headers: {
      Authorization: localStorage.getItem(AUTH_TOKEN),
    },
    method: method,
    body: file,
  })
    .then(response => processResponse(response))
    .then(data => {
      responseInstance.lastStatus.response = data;
      if (responseInstance.lastStatus.ok) {
        res = data;
        return res;
      } else {
        handleError(responseInstance.lastStatus);
      }
    })
    .catch(error => handleError(responseInstance.lastStatus));
};

export { get };
