import { useOktaAuth } from "@okta/okta-react";
import { useEffect } from "react";
import AxiosInstance, {
  onInterceptorResponseRejected,
  onInterceptorRequestFulfilled,
  onInterceptorRequestRejected,
} from "./FetchInterceptor";

const AxiosInterceptor = ({ children }) => {
  const { oktaAuth } = useOktaAuth();
  useEffect(() => {
    const respInterceptor = AxiosInstance.interceptors.response.use(
      response => response.data,
      error => onInterceptorResponseRejected(error, oktaAuth),
    );
    AxiosInstance.interceptors.request.use(onInterceptorRequestFulfilled, onInterceptorRequestRejected);

    return () => AxiosInstance.interceptors.response.eject(respInterceptor);
  }, [oktaAuth]);
  return children;
};

export default AxiosInterceptor;
