import React from "react";
import { Menu, Dropdown, Avatar } from "antd";
import { connect } from "react-redux";
import { UserOutlined, LogoutOutlined, MailOutlined } from "@ant-design/icons";
import { signOut } from "redux/actions/Auth";
import { useOktaAuth } from "@okta/okta-react";
import { useSelector } from "react-redux";
import { LOG_OUT } from "constants/CommonConstants";
import useIntlMessage from "hooks/useIntlMessage";

export const NavProfile = () => {
  const { oktaAuth } = useOktaAuth();
  const getMessage = useIntlMessage();
  const logout = async () => await oktaAuth.signOut("/");
  const userInfo = useSelector(state => state.myTeams.userInfo);

  const profileMenu = (
    <div className="nav-profile nav-dropdown">
      <div className="nav-profile-header">
        <div className="d-flex">
          <div className="pl-2">
            <h4 className="mb-0">{userInfo?.employeeName}</h4>
            <UserOutlined style={{ color: "#3f79f7" }} className="mr-1" />{" "}
            <span className="profile-text-color ">{userInfo?.employeeId}</span>
            <br />
            <MailOutlined style={{ color: "#3f79f7" }} className="mr-1" />{" "}
            <span className="profile-text-color">{userInfo?.emailId}</span>
          </div>
        </div>
      </div>
      <div className="nav-profile-body">
        <Menu>
          <Menu.Item onClick={logout}>
            <span>
              <LogoutOutlined />
              <span className="font-weight-normal">{getMessage(LOG_OUT.label)}</span>
            </span>
          </Menu.Item>
        </Menu>
      </div>
    </div>
  );
  return (
    <Dropdown placement="bottomRight" overlay={profileMenu} trigger={["click"]}>
      <Menu className="d-flex align-item-center" mode="horizontal">
        <Menu.Item key="profile">
          <Avatar icon={<UserOutlined />} />
        </Menu.Item>
      </Menu>
    </Dropdown>
  );
};

export default connect(null, { signOut })(NavProfile);
