import service from "../auth/FetchInterceptor";
import API_CONSTANTS from "../constants/ApiConstant";
import Utils from "../utils";

const commonService = {};

commonService.getMasterData = async function () {
  const { path, method } = API_CONSTANTS.GET_MASTER_DATA;
  try {
    const response = await service({
      url: path,
      method,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response;
  } catch (e) {
    console.error("Error in getting the master data.", e);
  }
};
