import React, { lazy, Suspense } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Loading from "components/shared-components/Loading";
import { AUTH_PREFIX_PATH } from "configs/AppConfig";
import lazyWithRetry from "utils/LazyWithRetry";

export const AppViews = () => {
  return (
    <Suspense fallback={<Loading cover="page" />}>
      <Switch>
        <Route
          path={`${AUTH_PREFIX_PATH}/login`}
          component={lazyWithRetry(() => import(`./authentication/login`), "login")}
        />
        <Route
          path={`${AUTH_PREFIX_PATH}/login-1`}
          component={lazyWithRetry(() => import(`./authentication/login-1`), "login-1")}
        />
        <Route
          path={`${AUTH_PREFIX_PATH}/login-2`}
          component={lazyWithRetry(() => import(`./authentication/login-2`), "login-2")}
        />
        <Route
          path={`${AUTH_PREFIX_PATH}/register-1`}
          component={lazyWithRetry(() => import(`./authentication/register-1`), "register-1")}
        />
        <Route
          path={`${AUTH_PREFIX_PATH}/register-2`}
          component={lazyWithRetry(() => import(`./authentication/register-2`), "register-2")}
        />
        <Route
          path={`${AUTH_PREFIX_PATH}/forgot-password`}
          component={lazyWithRetry(() => import(`./authentication/forgot-password`), "forgot-password")}
        />
        <Route
          path={`${AUTH_PREFIX_PATH}/error-1`}
          component={lazyWithRetry(() => import(`./errors/error-page-1`), "error-page-1")}
        />
        <Route
          path={`${AUTH_PREFIX_PATH}/error-2`}
          component={lazyWithRetry(() => import(`./errors/error-page-2`), "error-page-2")}
        />
        <Redirect from={`${AUTH_PREFIX_PATH}`} to={`${AUTH_PREFIX_PATH}/login`} />
      </Switch>
    </Suspense>
  );
};

export default AppViews;
