export function* handleGetRegionalData() {
  const params = {
    location: "All",
    account: "All",
  };
  try {
    const data = yield (dashboardServices.getRegionalData(params));
    const {headCountTrends, allocations, regions, count, description, title} = data
    let regionDesc = {count,description,title}
    yield put(setGlobalTrend(headCountTrends));
    yield put(setRegionData(regions));
    yield put(setAllocationData(allocations));
    yield put(setRegionDescData(regionDesc));
  } catch (e) {
    console.log("Unable to fetch regional Data", e);
  }
}


export default function* myTeamsSaga() {
  yield takeLatest(GET_MASTER_DATA, handleGetMaster);
  yield takeLatest(GET_DOMAIN_LIST, handleDomainList);
  yield takeLatest(GET_EMPLOYEE_DATA, handleGetEmployee);
  yield takeLatest(GET_ALL_LOCATIONS, handleAllLocations);
  yield takeLatest(GET_ALL_ACCOUNTS, handleGetAccounts);
  yield takeLatest(GET_ROLES, handleGetRoles);
  yield takeLatest(GET_DOMAINS, handleGetDomains);
  yield takeLatest(GET_SUB_DOMAINS, handleGetSubDomains);
  yield takeLatest(GET_REGIONAL_DATA, handleGetRegionalData)
}

