import { Grid, Menu } from "antd";
import { Tooltip } from "antd";
import navigationConfig from "configs/NavigationConfig";
import { NAV_TYPE_SIDE, SIDE_NAV_LIGHT } from "constants/ThemeConstant";
import useAuthRights from "hooks/useAuthRights";
import { useMemo, useState } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { onMobileNavToggle } from "redux/actions/Theme";
import utils from "utils";
import Icon from "../util-components/Icon";
import IntlMessage from "../util-components/IntlMessage";

const { SubMenu } = Menu;
const { useBreakpoint } = Grid;

const setLocale = (isLocaleOn, localeKey) => (isLocaleOn ? <IntlMessage id={localeKey} /> : localeKey.toString());

const setDefaultOpen = key => {
  let keyList = [];
  let keyString = "";
  if (key) {
    const arr = key.split("-");
    for (let index = 0; index < arr.length; index++) {
      const elm = arr[index];
      index === 0 ? (keyString = elm) : (keyString = `${keyString}-${elm}`);
      keyList.push(keyString);
    }
  }
  return keyList;
};
const subMenuKeys = navigationConfig.map(item => item.key);
const SideNavContent = props => {
  const [openKeys, setOpenKeys] = useState([]);
  const [activeClass, setActiveClass] = useState({
    key: "",
    selectedClassName: "ant-menu-item ant-menu-item-selected",
  });
  const { sideNavTheme, routeInfo, hideGroupTitle, localization, onMobileNavToggle } = props;

  const isMobile = !utils.getBreakPoint(useBreakpoint()).includes("lg");
  const closeMobileNav = () => {
    if (isMobile) {
      onMobileNavToggle(false);
    }
  };
  const onOpenChange = keys => {
    const latestOpenKey = keys.find(key => openKeys.indexOf(key) === -1);
    if (subMenuKeys.indexOf(latestOpenKey) === -1) {
      setOpenKeys(keys);
    } else {
      setOpenKeys(latestOpenKey ? [latestOpenKey] : []);
    }
  };
  const handleOnSelectSubMenu = menuKey => () => {
    if (isMobile) {
      setActiveClass({ ...activeClass, key: menuKey });
    }
  };
  const subMenuItem = subMenuFirst => {
    return (
      <span>
        {subMenuFirst.icon ? <Icon type={subMenuFirst?.icon} /> : null}
        <span>{setLocale(localization, subMenuFirst.title)}</span>
        <Link onClick={() => closeMobileNav()} to={subMenuFirst.path} />
      </span>
    );
  };
  return (
    <Menu
      theme={sideNavTheme === SIDE_NAV_LIGHT ? "light" : "dark"}
      mode="inline"
      style={{ height: "100%", borderRight: 0 }}
      defaultSelectedKeys={[routeInfo?.key]}
      selectedKeys={[routeInfo?.key]}
      defaultOpenKeys={setDefaultOpen(routeInfo?.key)}
      className={hideGroupTitle ? "hide-group-title" : ""}
      openKeys={openKeys}
      onOpenChange={onOpenChange}
    >
      {props.navigationOptions
        .filter(menu => !menu.notShow)
        .map(menu =>
          menu.submenu.length > 0 ? (
            <SubMenu
              icon={menu.icon ? <Icon type={menu?.icon} /> : null}
              key={menu.key}
              id={"menu_" + menu.key}
              title={setLocale(localization, menu.title)}
            >
              {menu.submenu.map(subMenuFirst => (
                <Menu.Item
                  className={subMenuFirst.key === activeClass.key ? activeClass.selectedClassName : ""}
                  onClick={handleOnSelectSubMenu(subMenuFirst.key)}
                  key={subMenuFirst.key}
                  id={"menu_" + subMenuFirst.key}
                >
                  {menu.key === "quick-allocations" ? (
                    <Tooltip key={menu.key} id={menu.key} title={<IntlMessage id={subMenuFirst.title} />}>
                      {subMenuItem(subMenuFirst)}
                    </Tooltip>
                  ) : (
                    subMenuItem(subMenuFirst)
                  )}
                </Menu.Item>
              ))}
            </SubMenu>
          ) : (
            <Menu.Item
              key={menu.key}
              id={"menu_" + menu.key}
              className={menu.key === activeClass.key ? activeClass.selectedClassName : ""}
              onClick={handleOnSelectSubMenu(menu.key)}
            >
              {menu.icon ? <Icon type={menu?.icon} /> : null}
              <span>{setLocale(localization, menu?.title)}</span>
              {menu.path ? <Link onClick={() => closeMobileNav() || setOpenKeys([])} to={menu.path} /> : null}
            </Menu.Item>
          ),
        )}

      {/* {props.navigationOptions
        .filter(menu => !menu.notShow)
        .map(menu =>
          menu.submenu.length > 0 ? (
            <SubMenu
              icon={menu.icon ? <Icon type={menu?.icon} /> : null}
              key={menu.key}
              id={"menu_" + menu.key}
              title={setLocale(localization, menu.title)}
            >
              {menu.submenu.map(subMenuFirst =>
                subMenuFirst.submenu.length > 0 ? (
                  <SubMenu
                    key={subMenuFirst.key}
                    popupClassName="top-nav-menu"
                    icon={subMenuFirst.icon ? <Icon type={subMenuFirst?.icon} /> : null}
                    title={setLocale(localization, subMenuFirst.title)}
                  >
                    {subMenuFirst.submenu.map(subMenuSecond => (
                      <Menu.Item key={subMenuSecond.key} id={"menu_" + subMenuSecond.key}>
                        {subMenuSecond.icon ? <Icon type={subMenuSecond?.icon} /> : null}
                        <span>{setLocale(localization, subMenuSecond.title)}</span>
                        <Link to={subMenuSecond.path} />
                      </Menu.Item>
                    ))}
                  </SubMenu>
                ) : (
                  <Menu.Item key={subMenuFirst.key} id={"menu_" + subMenuFirst.key}>
                    {subMenuFirst.icon ? <Icon type={subMenuFirst?.icon} /> : null}
                    <span>{setLocale(localization, subMenuFirst.title)}</span>
                    <Link to={subMenuFirst.path} />
                  </Menu.Item>
                ),
              )}
            </SubMenu>
          ) : (
            <Menu.Item
              key={menu.key}
              id={"menu_" + menu.key}
              className={menu.key === activeClass.key ? activeClass.selectedClassName : ""}
              onClick={handleOnSelectSubMenu(menu.key)}
            >
              {menu.icon ? <Icon type={menu?.icon} /> : null}
              <span>{setLocale(localization, menu?.title)}</span>
              {menu.path ? <Link onClick={() => closeMobileNav() || setOpenKeys([])} to={menu.path} /> : null}
            </Menu.Item>
          ),
        )} */}
    </Menu>
  );
};

const TopNavContent = props => {
  const { topNavColor, localization } = props;
  return (
    <Menu mode="horizontal" style={{ backgroundColor: topNavColor }}>
      {props.navigationOptions
        .filter(menu => !menu.notShow)
        .map(menu =>
          menu.submenu.length > 0 ? (
            <SubMenu
              key={menu.key}
              popupClassName="top-nav-menu"
              title={
                <span>
                  {menu.icon ? <Icon type={menu?.icon} /> : null}
                  <span>{setLocale(localization, menu.title)}</span>
                </span>
              }
            >
              {menu.submenu.map(subMenuFirst =>
                subMenuFirst.submenu.length > 0 ? (
                  <SubMenu
                    key={subMenuFirst.key}
                    popupClassName="top-nav-menu"
                    icon={subMenuFirst.icon ? <Icon type={subMenuFirst?.icon} /> : null}
                    title={setLocale(localization, subMenuFirst.title)}
                  >
                    {subMenuFirst.submenu.map(subMenuSecond => (
                      <Menu.Item key={subMenuSecond.key} id={"menu_" + subMenuSecond.key}>
                        <span>{setLocale(localization, subMenuSecond.title)}</span>
                        <Link to={subMenuSecond.path} />
                      </Menu.Item>
                    ))}
                  </SubMenu>
                ) : (
                  <Menu.Item key={subMenuFirst.key} id={"menu_" + subMenuFirst.key}>
                    {subMenuFirst.icon ? <Icon type={subMenuFirst?.icon} /> : null}
                    <span>{setLocale(localization, subMenuFirst.title)}</span>
                    <Link to={subMenuFirst.path} />
                  </Menu.Item>
                ),
              )}
            </SubMenu>
          ) : (
            <Menu.Item key={menu.key} id={"menu_" + menu.key}>
              {menu.icon ? <Icon type={menu?.icon} /> : null}
              <span>{setLocale(localization, menu?.title)}</span>
              {menu.path ? <Link to={menu.path} /> : null}
            </Menu.Item>
          ),
        )}
    </Menu>
  );
};

const MenuContent = props => {
  const { hasAssetsRights } = useAuthRights();
  const navigationOptions = useMemo(
    () =>
      navigationConfig.reduce((prevRoutes, currentMenuItem) => {
        if (currentMenuItem.submenu.length > 0) {
          const _currentMenuItem = { ...currentMenuItem };
          _currentMenuItem.submenu = _currentMenuItem.submenu.filter(subMenuItem =>
            subMenuItem.assetId ? hasAssetsRights(subMenuItem.assetId) : subMenuItem.isPublic,
          );
          if (_currentMenuItem.submenu.length > 0) {
            prevRoutes.push(_currentMenuItem);
          }
        } else if (currentMenuItem.assetId ? hasAssetsRights(currentMenuItem.assetId) : currentMenuItem.isPublic) {
          prevRoutes.push(currentMenuItem);
        }
        return prevRoutes;
      }, []),
    [hasAssetsRights],
  );

  return props.type === NAV_TYPE_SIDE ? (
    <SideNavContent {...props} navigationOptions={navigationOptions} />
  ) : (
    <TopNavContent {...props} navigationOptions={navigationOptions} />
  );
};

const mapStateToProps = ({ theme }) => {
  const { sideNavTheme, topNavColor } = theme;
  return { sideNavTheme, topNavColor };
};

export default connect(mapStateToProps, { onMobileNavToggle })(MenuContent);
