import { ACTIVE_STATUS } from "constants/CommonConstants";
import {
  SET_ALL_ACCOUNTS,
  SET_ALL_LOCATIONS,
  SET_DOMAIN_LIST,
  SET_EMPLOYEE_DATA,
  SET_MASTER_DATA,
  SET_ROLES,
  SET_USER_INFO,
  SET_DOMAINS,
  SET_SUB_DOMAINS,
  SET_COUNTRY_FOR_EMPLOYEE,
  SET_EMPLOYEE_DATA_FOR_COUNTRY,
  SET_STATUS_FOR_EMPLOYEE,
  SET_GLOBAL_TRENDS_DATA,
  SET_ALLOCATION_DATA,
  SET_REGIONS_DATA,
  SET_REGIONAL_DESC_DATA,
  SET_GLOBAL_MAP_DATA,
  SET_COUNTRY_RESERVE,
  SET_RESERVE_DATA,
  SET_RESERVE_FOR_ACCOUNT_ID,
  SET_VISIBILITY_COLUMNS,
} from "../constants/MyTeams";
const initState = {
  masterData: { skills: [], EmpType: [], designations: [], EmployeeStatus: [], FunctionalGrp: [] },
  domainList: [],
  domains: [],
  subdomains: [],
  employeeData: [],
  allLocations: [],
  accountsList: [],
  roles: [],
  loggedinId: null,
  userInfo: null,
  countryForEmployee: null,
  employeeDataByCountry: [],
  statusForEmployee: ACTIVE_STATUS.value,
  globalTrends: [],
  allocationData: [],
  regionData: [],
  regionDesc: null,
  globalMapData: [],
  reserveEmployeeCountry: null,
  reserveEmployeeData: null,
  reservedForAccountId: null,
  visibleColumns: null,
};

const myTeamsReducer = (state = initState, action) => {
  switch (action.type) {
    case SET_MASTER_DATA:
      return {
        ...state,
        masterData: action.payload,
      };
    case SET_EMPLOYEE_DATA:
      return {
        ...state,
        employeeData: action.payload,
      };
    case SET_DOMAIN_LIST:
      return {
        ...state,
        domainList: action.payload,
      };
    case SET_ALL_LOCATIONS:
      return {
        ...state,
        allLocations: action.payload,
      };
    case SET_ALL_ACCOUNTS:
      return {
        ...state,
        accountsList: action.payload,
      };
    case SET_USER_INFO:
      return {
        ...state,
        userInfo: action.payload,
        loggedinId: action.payload?.employeeId,
      };
    case SET_ROLES:
      return {
        ...state,
        roles: action.payload,
      };
    case SET_DOMAINS:
      return {
        ...state,
        domains: action.payload,
      };
    case SET_SUB_DOMAINS:
      return {
        ...state,
        subdomains: action.payload,
      };
    case SET_COUNTRY_FOR_EMPLOYEE:
      return {
        ...state,
        countryForEmployee: action.payload,
      };
    case SET_EMPLOYEE_DATA_FOR_COUNTRY:
      return {
        ...state,
        employeeDataByCountry: action.payload,
      };
    case SET_STATUS_FOR_EMPLOYEE:
      return {
        ...state,
        statusForEmployee: action.payload,
      };
    case SET_GLOBAL_TRENDS_DATA:
      return {
        ...state,
        globalTrends: action.payload,
      };
    case SET_ALLOCATION_DATA:
      return {
        ...state,
        allocationData: action.payload,
      };
    case SET_REGIONS_DATA:
      return {
        ...state,
        regionData: action.payload,
      };
    case SET_REGIONAL_DESC_DATA:
      return {
        ...state,
        regionDesc: action.payload,
      };
    case SET_GLOBAL_MAP_DATA:
      return {
        ...state,
        globalMapData: action.payload,
      };
    case SET_RESERVE_DATA:
      return {
        ...state,
        reserveEmployeeData: action.payload,
      };
    case SET_COUNTRY_RESERVE:
      return {
        ...state,
        reserveEmployeeCountry: action.payload,
      };
    case SET_RESERVE_FOR_ACCOUNT_ID:
      return {
        ...state,
        reservedForAccountId: action.payload,
      };
    case SET_VISIBILITY_COLUMNS:
      return {
        ...state,
        visibleColumns: action.payload,
      };
    default:
      return state;
  }
};

export default myTeamsReducer;
