import service from "auth/FetchInterceptor";
import API_CONSTANTS from "constants/ApiConstant";
import { orderBy, values } from "lodash";
import Utils from "utils";

const domainService = {};

domainService.uploadDocument = async function (payload) {
  const { path, method } = API_CONSTANTS.UPLOAD_DOMAIN_DOCUMENT;
  try {
    const response = await service({
      url: path,
      method,
      data: payload,
    });
    return response;
  } catch (error) {
    console.error("Something went wrong while uploading document", error);
  }
};
domainService.getList = async function (params) {
  const { path, method } = API_CONSTANTS.DOMAIN_LIST;
  try {
    const response = await service({
      url: path,
      method,
      params,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    const sortedRecords = orderBy(response.records, ["domainName"]);
    return sortedRecords;
  } catch (e) {
    console.error("Error in getting the domain list.", e);
  }
};

