const prepareDomainData = values => {
  return {
    accountId: values?.accountId,
    domainName: values?.domainName,
    deliveryManagers: values?.deliveryManagers,
    subDomains: values?.subDomains,
  };
};

const prepareUpdateDomainData = editDomainData => {
  return {
    id: editDomainData?.id,
    domainId: editDomainData?.domainId,
    domainName: editDomainData?.domainName,
    accountId: editDomainData?.accountId,
    deliveryManagers: editDomainData?.deliveryManagers,
    subDomains: editDomainData?.subDomains,
  };
};
domainService.flatDomainList = domainsList => {
  return domainsList.map(item => {
    return {
      ...item,
      subDomainsListForTabel: item?.subDomains?.join(", ") || "",
    };
  });
};

