const employeeService = {};

const getEmployeeIdFromStore = () => {
  return store.getState().myTeams.loggedinId;
};
const getEmployeeEmailFromStore = () => {
  return store.getState().myTeams.userInfo.emailId;
};

employeeService.getList = async function (params) {
  const { path, method } = API_CONSTANTS.EMPLOYEE_LIST;
  try {
    const response = await service({
      url: path,
      method,
      params,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response.records;
  } catch (e) {
    console.error("Error in getting the employee list.", e);
  }
};
employeeService.getAllEmployee = async function () {
  const { path, method } = API_CONSTANTS.GET_ALL_EMPLOYEES;
  try {
    const response = await service({
      url: path,
      method,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }

    const sortedRecords = orderBy(response.records, ["employeeName"]);
    return sortedRecords;
  } catch (e) {
    console.error("Error in getting the employee list.", e);
  }
};
