export const getFlatAllocations = allocations =>
  allocations.map(allocation => ({
    projectName: allocation?.project?.projectName,
    domainName: allocation?.domain?.domainName,
    accountName: allocation?.account?.accountName,
    startDate: allocation?.startDate,
    subDomainName: allocation?.subDomain?.subDomainName,
    defaultReservedEndDate: allocation?.defaultReservedEndDate,
    allocationStatus: allocation?.allocationStatus,
    allocationSubStatus: allocation?.allocationSubStatus,
    assignedLevel: allocation?.assignedLevel,
    reservationExpiryDate: allocation?.reservationExpiryDate,
  }));

const prepareAddEmpData = values => {
  return {
    employeeId: values?.employeeId,
    employeeName: values?.employeeName,
    gender: values?.gender,
    mobileNumber: values?.mobileNumber,
    emailId: values?.emailId,
    previousExperience: values?.previousExperience,
    role: values?.role,
    country: values?.country,
    empLocation: values?.empLocation,
    designation: values?.designation,
    functionalGroup: values?.functionalGroup,
    empStatus: "Active",
    employmentType: values?.employmentType,
    dateOfJoining: values?.dateOfJoining ? moment(values?.dateOfJoining).format(NEW_DATE_FORMAT).valueOf() : null,
    dateOfBirth: values?.dateOfBirth ? moment(values?.dateOfBirth).format(NEW_DATE_FORMAT).valueOf() : null,
    hasPassort: values?.hasPassort,
    hasB1: values?.hasB1,
    passportExpiryDate: values?.passportExpiryDate,
    b1ExpiryDate: values?.b1ExpiryDate,
    hrmsId: values?.hrmsId,
    //baseTechnology: values?.baseTechnology.toString(),
    parentAccountName: values?.parentAccountName,
    assignedLevel: values?.assignedLevel?.toUpperCase(),
    nonDeployable: values?.nonDeployable,
  };
};
const prepareUpdateParentAccount = (values, employeeToEdit) => {
  return {
    employeeId: values?.employeeId,
    parentAccountId: values?.parentAccountId,
    parentAccountName: values?.parentAccountName,
  };
};
const prepareDeactivatedAccountData = values => {
  return {
    employeeId: values?.employeeId,
    endDate: values?.endDate ? moment(values?.endDate).format(NEW_DATE_FORMAT).valueOf() : null,
    exitType: values?.exitType,
    exitReason: values?.exitReason,
  };
};
const prepareUpdateEmpData = (values, employeeToEdit) => {
  return {
    employeeId: values?.employeeId,
    employeeName: values?.employeeName,
    parentAccountName: values?.parentAccountName,
    gender: values?.gender,
    mobileNumber: values?.mobileNumber,
    emailId: values?.emailId,
    previousExperience: values?.previousExperience,
    role: values?.role,
    country: values?.country,
    empLocation: values?.empLocation,
    designation: values?.designation,
    functionalGroup: values?.functionalGroup,
    empStatus: values?.empStatus,
    employmentType: values?.employmentType,
    dateOfJoining: values?.dateOfJoining ? moment(values.dateOfJoining).format(NEW_DATE_FORMAT).valueOf() : null,
    dateOfBirth: values?.dateOfBirth ? moment(values.dateOfBirth).format(NEW_DATE_FORMAT).valueOf() : null,
    hasPassort: values?.hasPassort,
    hasB1: values?.hasB1,
    passportExpiryDate: values.passportExpiryDate
      ? moment(values.passportExpiryDate).format(NEW_DATE_FORMAT).valueOf()
      : null,
    b1ExpiryDate: values.b1ExpiryDate ? moment(values.b1ExpiryDate).format(NEW_DATE_FORMAT).valueOf() : null,
    hrmsId: values?.hrmsId,
    endDate: values.endDate ? moment(values.endDate).format(NEW_DATE_FORMAT).valueOf() : null,
    empSubStatus: {
      employeeID: values?.employeeId,
      subStatus: values?.subStatus,
      fromDate: values?.fromDate,
      toDate: values?.toDate,
    },
    assignedLevel: values?.assignedLevel?.toUpperCase(),
    nonDeployable:
      typeof values?.nonDeployable === "boolean"
        ? values?.nonDeployable
        : values?.nonDeployable === "Yes"
        ? true
        : false,
  };
};
const prepareProfileData = profileUpdateData => {
  return {
    employeeId: profileUpdateData?.employeeId,
    alternateMobileNumber: profileUpdateData?.alternateMobileNumber,
    mobileNumber: profileUpdateData?.mobileNumber,
    personalEmailId: profileUpdateData?.personalEmailId,
    baseTechnology: profileUpdateData?.baseTechnology.toString(),
    technologyKnown: profileUpdateData?.technologyKnown,
  };
};
const prepareAddAllocationPayload = formData => ({
  employeeId: formData.empId,
  projectId: formData.projectName || null,
  domainId: formData.domainName || null,
  subDomainId: formData.subdomain || null,
  reservedForAccountId: formData.accountName,
  allocationStatus: formData.allocationStatus?.toUpperCase(),
  assignedLevel: formData.allocationLevel?.toUpperCase(),
  // startDate: new Date(formData.startDate),
  startDate: formData.startDate ? moment(formData.startDate).format(NEW_DATE_FORMAT).valueOf() : null,
  notes: formData.notes || "",
  leverURL: formData.leverURL || "",
});
const prepareEmployeeSubStatus = statusData => ({
  employeeID: statusData?.employeeId,
  subStatus: statusData?.subStatus,
  fromDate: statusData?.fromDate ? moment(statusData?.fromDate).format(NEW_DATE_FORMAT).valueOf() : null,
  toDate: statusData?.toDate ? moment(statusData?.toDate).format(NEW_DATE_FORMAT).valueOf() : null,
});
employeeService.prepareEmployeeList = employeeList => {
  return employeeList.map(employee => {
    return {
      ...employee,
      nonDeployable: employee?.nonDeployable === true ? "Yes" : "No",
      allocationStatus: employee.employeeAllocationDataDTO?.allocationStatus || employee?.allocationStatus,
      reservedForAccount:
        employee.employeeAllocationDataDTO?.reservedFor?.accountName || employee?.reservedForAccountName,
      parentAccount: employee.employeeAllocationDataDTO?.parentAccount?.accountName || employee?.parentAccountName,
    };
  });
};
