import { SHOW_LOADING } from "redux/constants/Auth";

const initialState = {
  isLoading: false,
};

const Loader = (state = initialState, action) => {
  switch (action.type) {
    case SHOW_LOADING: {
      return {
        ...state,
        isLoading: action.isLoading,
      };
    }
    default:
      return state;
  }
};

export default Loader;
