import { ACTIVE_STATUS } from "constants/CommonConstants";
import {
  SET_ALLOCATIONCHANGE_COLUMNS,
  SET_BENCHREPORT_COLUMNS,
  SET_BILLABLEAGING_COLUMNS,
  SET_DASHBBOARDREPORT_COLUMNS,
  SET_EMPLOYEEEXIT_REPORT_COLUMNS,
  SET_EMPLOYEEJOURNEY_COLUMNS,
  SET_HEADCOUNT_COLUMNS,
  SET_MONTHLY_ALLOCATION_COLUMNS,
  SET_PROJECTRELEASE_COLUMNS,
  SET_SUBSTATUSREPORT_COLUMNS,
  SET_UTILLIZATION_COLUMNS,
  SET_RAMP_UP_DOWN_COLUMNS,
  SET_TARGET_DATA_REPORT_COLUMNS,
} from "../constants/MyTeams";
const initState = {
  dashboardReportColumns: null,
  utillizationReportColumns: null,
  headCountReportColumns: null,
  benchReserveReportColumns: null,
  allocationChangeReportColumns: null,
  billableAgingReportColumns: null,
  employeeJourneyReportColumns: null,
  projectReleaseReportColumns: null,
  employeeExitReportColumns: null,
  subStatusReportColumns: null,
  monthlyAllocationReportColumns: null,
  rampUpDownReportColumns: null,
  targetDataReportColumns: null,
};

const reportsReducer = (state = initState, action) => {
  switch (action.type) {
    case SET_DASHBBOARDREPORT_COLUMNS:
      return {
        ...state,

        dashboardReportColumns: action.payload,
      };
    case SET_UTILLIZATION_COLUMNS:
      return {
        ...state,
        utillizationReportColumns: action.payload,
      };
    case SET_HEADCOUNT_COLUMNS:
      return {
        ...state,
        headCountReportColumns: action.payload,
      };
    case SET_BENCHREPORT_COLUMNS:
      return {
        ...state,
        benchReserveReportColumns: action.payload,
      };
    case SET_ALLOCATIONCHANGE_COLUMNS:
      return {
        ...state,
        allocationChangeReportColumns: action.payload,
      };
    case SET_BILLABLEAGING_COLUMNS:
      return {
        ...state,
        billableAgingReportColumns: action.payload,
      };
    case SET_EMPLOYEEJOURNEY_COLUMNS:
      return {
        ...state,
        employeeJourneyReportColumns: action.payload,
      };
    case SET_PROJECTRELEASE_COLUMNS:
      return {
        ...state,
        projectReleaseReportColumns: action.payload,
      };
    case SET_MONTHLY_ALLOCATION_COLUMNS:
      return {
        ...state,
        monthlyAllocationReportColumns: action.payload,
      };
    case SET_EMPLOYEEEXIT_REPORT_COLUMNS:
      return {
        ...state,
        employeeExitReportColumns: action.payload,
      };
    case SET_SUBSTATUSREPORT_COLUMNS:
      return {
        ...state,
        subStatusReportColumns: action.payload,
      };
    case SET_TARGET_DATA_REPORT_COLUMNS:
      return {
        ...state,
        targetDataReportColumns: action.payload,
      };
    case SET_RAMP_UP_DOWN_COLUMNS:
      return {
        ...state,
        rampUpDownReportColumns: action.payload,
      };
    default:
      return state;
  }
};

export default reportsReducer;
