import service from "auth/FetchInterceptor";
import API_CONSTANTS from "constants/ApiConstant";
import { MESSAGES, STATUSES } from "constants/ManageAccounts";
import store from "redux/store";
import Utils from "utils";

const ManageAccountsService = {};

const getEmployeeIdFromStore = () => {
  return store.getState().myTeams.loggedinId;
};

ManageAccountsService.getAccountsListByEmpId = async function (params) {
  const { path, method } = API_CONSTANTS.ACCOUNTS_LIST;
  if (getEmployeeIdFromStore()) {
    try {
      const response = await service({
        url: `${path}${getEmployeeIdFromStore()}`,
        method,
        params,
      });
      if (Utils.isEmpty(response)) {
        return [];
      }
      const sortedRecords = Utils.sortArray(response.records, ["accountName"]);
      return sortedRecords;
    } catch (e) {
      console.error("Error in getting the Accounts List.", e);
    }
  }
};

ManageAccountsService.getList = async function (params) {
  const { path, method } = API_CONSTANTS.GET_ACCOUNTS;
  try {
    const response = await service({
      url: path,
      method,
      params,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    const sortedRecords = Utils.sortArray(response.records, ["accountName"]);
    return sortedRecords;
  } catch (e) {
    console.error("Error in getting the Accounts List.", e);
  }
};
ManageAccountsService.CreateAccount = async function (data) {
  const { path, method } = API_CONSTANTS.CREATE_ACCOUNT;
  try {
    // need to fix the PUT request at backend
    const response = await service({
      url: path,
      method,
      data,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response;
  } catch (e) {
    console.error("Error while creating Account", e);
  }
};
ManageAccountsService.Update = async function (accountId, data) {
  const { path, method } = API_CONSTANTS.UPDATE_ACCOUNT;
  try {
    //   need to fix the PUT request at backend
    const response = await service({
      url: `${path}${accountId}`,
      method,
      data,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response;
  } catch (e) {
    console.error("Error while updating Account", e);
  }
};
ManageAccountsService.prepareDataForAddAccount = (values, clientCountryLead) => {
  return {
    accountName: values?.accountName,
    industryType: values?.industryType,
    clientAddress: values?.clientAddress,
    deliveryManagers: [],
    clientPartner: { employeeId: values?.clientPartner },
    clientDeliveryHead: { employeeId: values?.clientDeliveryHead },
    clientPortfolioManager: { employeeId: values?.clientPortfolioManager },
    clientCountryLeads: clientCountryLead,
    status: STATUSES.ACTIVE_STATUS.value,
  };
};
ManageAccountsService.prepareDataForUpdateAccount = (editAccounts, values, clientCountryLead) => {
  return {
    accountId: editAccounts?.accountId,
    accountName: values?.accountName,
    industryType: values?.industryType,
    clientAddress: values?.clientAddress,
    clientPartner: { employeeId: values?.clientPartner },
    deliveryManagers: [],
    clientDeliveryHead: { employeeId: values?.clientDeliveryHead },
    clientPortfolioManager: { employeeId: values?.clientPortfolioManager },
    clientCountryLeads: clientCountryLead,
    status: values?.status,
  };
};

ManageAccountsService.performDataValidations = formData => {
  if (Utils.isEmpty(formData.ccls)) {
    return MESSAGES.CCL_EMPTY;
  }
  if (checkForDuplicateCCls(formData.ccls)) {
    return MESSAGES.CCL_DUPLICATE;
  }

  if (validateFunctionalGroups(formData.ccls)) {
    return MESSAGES.DUPLICATE_FUNCTIONAL_GROUP;
  }

  return null;
};

const checkForDuplicateCCls = ccls => {
  return Utils.hasDuplicates(ccls, "employeeId");
};
