function validateFunctionalGroups(array) {
  const regionGroupsMap = {};

  for (const entry of array) {
    const { region, functionalGroups } = entry;

    if (!regionGroupsMap[region]) {
      regionGroupsMap[region] = new Set(functionalGroups);
    } else {
      const existingGroups = regionGroupsMap[region];
      for (const group of functionalGroups) {
        if (existingGroups.has(group)) {
          return true; // Duplicate functional groups found
        }
      }
      existingGroups.add(...functionalGroups);
    }
  }

  return false; // No duplicate functional groups found
}





export default ManageAccountsService;
