import jsPDF from "jspdf";
import "jspdf-autotable";
import { upperCase } from "lodash";
import { utils, writeFile, writeFileXLSX } from "xlsx";

class FileHandler {
  static exportToExcel(data, fileName) {
    const ws = utils.json_to_sheet(data);
    const wb = utils.book_new();
    utils.book_append_sheet(wb, ws, "Data");
    writeFileXLSX(wb, `${fileName}.xlsx`);
  }

  static exportToCSV(data, fileName) {
    const ws = utils.json_to_sheet(data);
    const csv = utils.sheet_to_csv(ws);
    const wb = utils.book_new();
    utils.book_append_sheet(wb, csv, "Data");
    writeFile(wb, `${fileName}.csv`);
  }

  static exportToExcelCustomHeader(data, fileName, dateHeader = "") {
    const wb = utils.book_new();
    const ws = utils.json_to_sheet([]);
    const ORIGIN = dateHeader ? "A2" : "A1";

    if (dateHeader) {
      // Create a merged header row
      const mergedHeaderRow = [[dateHeader]];

      // Add the merged header row to the worksheet
      utils.sheet_add_aoa(ws, mergedHeaderRow, { origin: "A1" });

      const numOfCols = Object.keys(data[0]).length;

      // Create a merges property to specify the merged header range
      ws["!merges"] = [{ s: { r: 0, c: 0 }, e: { r: 0, c: numOfCols - 1 } }];
    }

    //Starting in the second row to avoid overriding and skipping headers
    utils.sheet_add_json(ws, data, { origin: ORIGIN, skipHeader: true });

    utils.book_append_sheet(wb, ws, "Sheet1");
    writeFileXLSX(wb, `${fileName}.xlsx`);
  }

  // static exportToPDF(
  //   headers = [],
  //   jsonData = [],
  //   configs = { autoSize: true },
  //   width = null,
  //   hight = null,
  //   fileName = "PDF",
  //   columnWidth = 50,
  // )
  // {
  //   const pdf = new jsPDF({ format: [width, hight], putOnlyUsedFonts: true, orientation: "landscape" });
  //   const columnHeaders = [];
  //   for (let i = 0; i < headers.length; i += 1) {
  //     columnHeaders.push({
  //       id: headers[i],
  //       name: headers[i],
  //       prompt: upperCase(headers[i]),
  //       width: columnWidth,
  //       align: "center",
  //     });
  //   }
  //   pdf.table(15, 15, jsonData, columnHeaders, configs);
  //   window.open(pdf.output("bloburi", { filename: fileName }), "_blank");
  // }

  static exportToPDF(
    headers = [],
    jsonData = [],
    configs = { autoSize: true },
    width = null,
    hight = null,
    fileName = "PDF",
    columnWidth = 50,
    dateHeader = "",
  ) {
    const pdf = new jsPDF({ format: [width, hight], putOnlyUsedFonts: true, orientation: "landscape" });

    const pdfHead = [];

    if (dateHeader) {
      pdfHead.push([{ content: dateHeader, colSpan: headers.length, styles: { halign: "center" } }]);
    }

    const columnHeader = [];
    for (let i = 0; i < headers.length; i += 1) {
      columnHeader.push({
        content: upperCase(headers[i].value),
        minCellWidth: columnWidth,
      });
    }

    pdfHead.push(columnHeader);

    // Set the properties and add the tables
    pdf.setProperties({
      title: fileName,
    });

    function convertDataToTableFormat(data) {
      const keys = Object.keys(data[0]);
      return data.map(item => keys.map(key => (item[key] !== undefined ? item[key].toString() : "")));
    }
    const bodyData = jsonData ? convertDataToTableFormat(jsonData) : [];

    pdf.autoTable({
      head: pdfHead,
      body: bodyData,
      headStyles: { fillColor: [2, 179, 226] },
      startY: 15, // Y position to start the table
      theme: "grid", // Optional: 'striped', 'grid', 'plain'
    });

    // Move to the next page if the table exceeds the page height
    if (pdf.previousAutoTable.finalY > pdf.internal.pageSize.height) {
      pdf.addPage();
    }

    window.open(pdf.output("bloburi"), "_blank");

    pdf.save(fileName);
  }
}

export default FileHandler;
