import { useOktaAuth } from "@okta/okta-react";
import Loading from "components/shared-components/Loading";
import { APP_PREFIX_PATH } from "configs/AppConfig";
import navigationConfig from "configs/NavigationConfig";
import { DASHBOARD_ALLOWED_ROLES, ROLE_EMPLOYEE } from "constants/CommonConstants";
import useAuthRights from "hooks/useAuthRights";
import React, { lazy, Suspense, useCallback, useEffect, useMemo } from "react";
import { useDispatch } from "react-redux";
import { Redirect, Route, Switch, useHistory } from "react-router-dom";
import {
  getAllAccounts,
  getAllLocations,
  getDomainList,
  getDomains,
  getEmployeeData,
  getMasterData,
  getRegionalData,
  getRoles,
  getSubDomains,
  setUserInfo,
} from "redux/actions";
import { AUTH_TOKEN } from "redux/constants/Auth";
import { EmployeeService } from "services";
import { showLoader } from "../../redux/actions/Loader";
import lazyWithRetry from "utils/LazyWithRetry";

export const AppViews = () => {
  const dispatch = useDispatch();
  const { authState } = useOktaAuth();
  const { hasAssetsRights } = useAuthRights();
  const history = useHistory();
  const getLoggedInUser = useCallback(
    claims => {
      dispatch(showLoader(true));
      EmployeeService.getEmployeeByEmail(claims?.email)
        .then(response => {
          const { employee, roleAssetPrivilegeVO } = response.records;
          const userInfo = { ...employee, roleAssetPrivilegeVO };
          dispatch(setUserInfo(userInfo));
          if (userInfo.role !== ROLE_EMPLOYEE) {
            dispatch(getAllAccounts());
            dispatch(getDomainList());
            dispatch(getRoles());
            dispatch(getAllLocations());
            dispatch(getEmployeeData());
            dispatch(getDomains());
            dispatch(getSubDomains());
            if (DASHBOARD_ALLOWED_ROLES.includes(userInfo.role)) {
              dispatch(getRegionalData());
            }
          }
          const previousLocation = localStorage.getItem("previousLocation", window.location.href);
          if (previousLocation) {
            localStorage.removeItem("previousLocation");
            history.push(previousLocation);
          }
        })
        .catch(e => {
          console.error("Error in getting logged in user details", e);
        })
        .finally(() => {
          dispatch(showLoader(false));
        });
    },
    [dispatch],
  );

  useEffect(() => {
    if (authState?.isAuthenticated) {
      localStorage.setItem(AUTH_TOKEN, "Bearer " + authState.idToken.idToken);
      getLoggedInUser(authState.idToken.claims);
      dispatch(getMasterData());
    }
  }, [authState, dispatch, getLoggedInUser]);

  const FILTERED_APP_ROUTES = useMemo(
    () =>
      navigationConfig.reduce((prevRoutes, currentMenuItem) => {
        if (currentMenuItem.submenu.length > 0) {
          currentMenuItem.submenu.forEach(subMenuItem => {
            if (subMenuItem.assetId ? hasAssetsRights(subMenuItem.assetId) : subMenuItem.isPublic) {
              prevRoutes.push({ ...subMenuItem, componentPath: `${currentMenuItem.key}/${subMenuItem.key}` });
            }
          });
        } else if (currentMenuItem.assetId ? hasAssetsRights(currentMenuItem.assetId) : currentMenuItem.isPublic) {
          prevRoutes.push({ ...currentMenuItem, componentPath: `${currentMenuItem.key}` });
        }
        return prevRoutes;
      }, []),
    [hasAssetsRights],
  );

  window.addEventListener("beforeunload", () => {
    let search = window.location.search;
    let path = window.location.pathname;
    let params = search ? path + search : path;
    localStorage.setItem("previousLocation", params);
  });

  return (
    <Suspense fallback={<Loading cover="content" />}>
      <Switch>
        {FILTERED_APP_ROUTES.map(route => (
          <Route
            key={route.key}
            path={route.path}
            component={lazyWithRetry(() => import(`./${route.componentPath}`), route.key)}
          />
        ))}
        <Redirect from={`${APP_PREFIX_PATH}`} to={`${APP_PREFIX_PATH}/myproject-allocations`} />
      </Switch>
    </Suspense>
  );
};

export default React.memo(AppViews);
