import { HEADERS_NAME_FOR_EXPORT } from "constants/CommonConstants";
import { MENU_ITEMS_TYPES } from "constants/EmployeeLoginDetails";
import _ from "lodash";
import moment from "moment";
import { DATE_FORMAT_DD_MM_YYYY, REPORT_DATE_FORMAT_YYYY_MM_DD } from "../constants/DateConstant";

import FileHandler from "./fileHandler";
import { CURRENTLY_ACTIVE } from "constants/BillableAgingConstants";
import { TARGET_DATA_FILE_NAME } from "constants/TargetDataReportConstant";
import { RAMP_FILE_NAME } from "constants/RampUpDownReportConstant";
import { notification } from "antd";

const ALL = "All";
const allAgingFilter = "-1";
class Utils {
  /**
   * Get first character from first & last sentences of a username
   * @param {String} name - Username
   * @return {String} 2 characters string
   */
  static getNameInitial(name) {
    let initials = name.match(/\b\w/g) || [];
    return ((initials.shift() || "") + (initials.pop() || "")).toUpperCase();
  }

  /**
   * Get current path related object from Navigation Tree
   * @param {Array} navTree - Navigation Tree from directory 'configs/NavigationConfig'
   * @param {String} path - Location path you looking for e.g '/app/dashboards/analytic'
   * @return {Object} object that contained the path string
   */
  static getRouteInfo(navTree, path) {
    if (navTree.path === path) {
      return navTree;
    }
    let route;
    for (let p in navTree) {
      if (navTree.hasOwnProperty(p) && typeof navTree[p] === "object") {
        route = this.getRouteInfo(navTree[p], path);
        if (route) {
          return route;
        }
      }
    }
    return route;
  }

  /**
   * Get accessible color contrast
   * @param {String} hex - Hex color code e.g '#3e82f7'
   * @return {String} 'dark' or 'light'
   */
  static getColorContrast(hex) {
    if (!hex) {
      return "dark";
    }
    const threshold = 130;
    const hRed = hexToR(hex);
    const hGreen = hexToG(hex);
    const hBlue = hexToB(hex);
    function hexToR(h) {
      return parseInt(cutHex(h).substring(0, 2), 16);
    }
    function hexToG(h) {
      return parseInt(cutHex(h).substring(2, 4), 16);
    }
    function hexToB(h) {
      return parseInt(cutHex(h).substring(4, 6), 16);
    }
    function cutHex(h) {
      return h.charAt(0) === "#" ? h.substring(1, 7) : h;
    }
    const cBrightness = (hRed * 299 + hGreen * 587 + hBlue * 114) / 1000;
    if (cBrightness > threshold) {
      return "dark";
    } else {
      return "light";
    }
  }

  /**
   * Darken or lighten a hex color
   * @param {String} color - Hex color code e.g '#3e82f7'
   * @param {Number} percent - Percentage -100 to 100, positive for lighten, negative for darken
   * @return {String} Darken or lighten color
   */
  static shadeColor(color, percent) {
    let R = parseInt(color.substring(1, 3), 16);
    let G = parseInt(color.substring(3, 5), 16);
    let B = parseInt(color.substring(5, 7), 16);
    R = parseInt((R * (100 + percent)) / 100);
    G = parseInt((G * (100 + percent)) / 100);
    B = parseInt((B * (100 + percent)) / 100);
    R = R < 255 ? R : 255;
    G = G < 255 ? G : 255;
    B = B < 255 ? B : 255;
    const RR = R.toString(16).length === 1 ? `0${R.toString(16)}` : R.toString(16);
    const GG = G.toString(16).length === 1 ? `0${G.toString(16)}` : G.toString(16);
    const BB = B.toString(16).length === 1 ? `0${B.toString(16)}` : B.toString(16);
    return `#${RR}${GG}${BB}`;
  }

  /**
   * Convert RGBA to HEX
   * @param {String} rgba - RGBA color code e.g 'rgba(197, 200, 198, .2)')'
   * @return {String} HEX color
   */
  static rgbaToHex(rgba) {
    const trim = str => str.replace(/^\s+|\s+$/gm, "");
    const inParts = rgba.substring(rgba.indexOf("(")).split(","),
      r = parseInt(trim(inParts[0].substring(1)), 10),
      g = parseInt(trim(inParts[1]), 10),
      b = parseInt(trim(inParts[2]), 10),
      a = parseFloat(trim(inParts[3].substring(0, inParts[3].length - 1))).toFixed(2);
    const outParts = [
      r.toString(16),
      g.toString(16),
      b.toString(16),
      Math.round(a * 255)
        .toString(16)
        .substring(0, 2),
    ];

    outParts.forEach(function (part, i) {
      if (part.length === 1) {
        outParts[i] = "0" + part;
      }
    });
    return `#${outParts.join("")}`;
  }

  /**
   * Returns either a positive or negative
   * @param {Number} number - number value
   * @param {any} positive - value that return when positive
   * @param {any} negative - value that return when negative
   * @return {any} positive or negative value based on param
   */
  static getSignNum(number, positive, negative) {
    if (number > 0) {
      return positive;
    }
    if (number < 0) {
      return negative;
    }
    return null;
  }

  /**
   * Returns either ascending or descending value
   * @param {Object} a - antd Table sorter param a
   * @param {Object} b - antd Table sorter param b
   * @param {String} key - object key for compare
   * @return {any} a value minus b value
   */
  static scrollIntoColumn = index => {
    const headerScroll = document.getElementById(index);
    headerScroll?.scrollIntoView({ behavior: "smooth", block: "nearest", inline: "start" });
  };
  static antdTableSorter(a, b, key) {
    if (this.isEmpty(a && a[key])) return -1;
    if (this.isEmpty(b && b[key])) return 1;
    if (!isNaN(Number(a[key])) && !isNaN(Number(b[key]))) {
      return a[key] - b[key];
    }
    if (typeof a[key] === "string" && typeof b[key] === "string") {
      a = a[key].toLowerCase();
      b = b[key].toLowerCase();
      return a > b ? 1 : b > a ? -1 : 0;
    }
    return;
  }

  /**
   * Filter array of object
   * @param {Array} list - array of objects that need to filter
   * @param {String} key - object key target
   * @param {any} value  - value that excluded from filter
   * @return {Array} a value minus b value
   */
  static filterArray(list, key, value) {
    let data = list;
    if (list) {
      data = list.filter(item => item[key] === value);
    }
    return data;
  }

  /**
   * Remove object from array by value
   * @param {Array} list - array of objects
   * @param {String} key - object key target
   * @param {any} value  - target value
   * @return {Array} Array that removed target object
   */
  static deleteArrayRow(list, key, value) {
    let data = list;
    if (list) {
      data = list.filter(item => item[key] !== value);
    }
    return data;
  }

  /**
   * Wild card search on all property of the object
   * @param {Number | String} input - any value to search
   * @param {Array} list - array for search
   * @return {Array} array of object contained keyword
   */
  static wildCardSearch(list, input) {
    const searchText = item => {
      for (let key in item) {
        if (item[key] == null) {
          continue;
        }
        if (item[key].toString().toUpperCase().indexOf(input.toString().toUpperCase()) !== -1) {
          return true;
        }
      }
    };
    list = list.filter(value => searchText(value));
    return list;
  }
  /**
   * Checks if the array has duplicate elements
   * @param {Array} arr - Array to check
   * @param {String} key - Key of the object to compare (optional)
   * @return {Boolean} true if array has duplicates
   */
  static hasDuplicates(arr, key = null) {
    if (this.isEmpty(arr) || arr.length < 2) {
      return false;
    }
    let arrToCheck = arr;
    if (key) {
      arrToCheck = arr.map(x => x[key]);
    }
    return Array.from(new Set(arrToCheck)).length !== arrToCheck.length;
  }

  /**
   * Get Breakpoint
   * @param {Object} screens - Grid.useBreakpoint() from antd
   * @return {Array} array of breakpoint size
   */
  static getBreakPoint(screens) {
    let breakpoints = [];
    for (const key in screens) {
      if (screens.hasOwnProperty(key)) {
        const element = screens[key];
        if (element) {
          breakpoints.push(key);
        }
      }
    }
    return breakpoints;
  }
  static isEmpty(value) {
    return value === null || value === undefined || value?.length === 0;
  }
  static sortArray(array, keys = null) {
    if (!Array.isArray(array) && array !== null) {
      return _.sortBy(array);
    }
    if (this.isEmpty(keys)) {
      let lowercased = array?.map(e => {
        return e?.toLowerCase();
      });
      return _.sortBy(lowercased).map(val => _.capitalize(val));
    }
    return _.orderBy(array, [item => _.get(item, keys)?.toLowerCase()]);
  }

  // static blockInvalidChar = e => ["e", "E", "+", "-"].includes(e.key) && e.preventDefault();
  static blockInvalidChar = e => !/[\d+0-9]/.test(e.key) && e.preventDefault();
  static preExperienceblockInvalidChar = e => !/[.0-9]/.test(e.key) && e.preventDefault();

  static truncateText(value, len = 15) {
    if (this.isEmpty(value)) return value;
    const _value = String(value);
    return _value.length > len ? `${_value.substring(0, len)}...` : _value;
  }
  static maskCharacters = str => {
    if (str?.length <= 4) {
      return str;
    } else {
      return "*".repeat(str?.length - 4) + str?.slice(-4);
    }
  };
  static disableFutureDates = currentDate => {
    return currentDate && currentDate > moment().endOf("day");
  };

  static disableBeforeAfterDate = (current, afterDate) => {
    return (current && current.isBefore(afterDate)) || current > moment().endOf("day");
  };

  static onAccountSelection = (value, form, fieldName) => {
    if (value === ALL) {
      form.setFieldsValue({ account: [ALL] });
    } else {
      const selectedValue = [...form.getFieldValue(fieldName)];
      form.setFieldsValue({ [fieldName]: selectedValue.filter(x => x !== ALL) });
    }
  };

  static toSentenceCase = camelCase => {
    if (camelCase) {
      const result = camelCase.replace(/([A-Z])/g, " $1");
      return result[0].toUpperCase() + result.substring(1).toUpperCase();
    }
    return "";
  };

  static getDynamicHeadersForPDF = (data = []) =>
    data.map(col => {
      return { key: col.dataIndex, value: col?.title?.props?.title };
    });

  static getDynamicHeadersForPDFNested = (data = []) =>
    data.map(col => {
      return { key: col.dataIndex, value: col.title };
    });

  static getDynamicHeadersForExcel = (columns = [], filterVisibleColumns, exportAllData) => {
    let header = {};
    columns.forEach(item => {
      if (exportAllData || (filterVisibleColumns && filterVisibleColumns[item.key])) {
        header[item.key] = item.title.toUpperCase();
      }
    });

    return header;
  };

  static makeHeaderDataForExcel = item => {
    const headers = {};
    Object.keys(item).forEach(key => {
      if (item[key]) {
        headers[key] = HEADERS_NAME_FOR_EXPORT[key];
      }
    });
    return headers;
  };

  static sortArrayByDate(array, key, direction = "desc") {
    if (Array.isArray(array) && array !== null) {
      return _.orderBy(
        array,
        [
          i => {
            return new Date(i[key]);
          },
        ],
        [direction],
      );
    }
  }

  static createObjFromArray = array => {
    return array.reduce((acc, cur) => ({ ...acc, [cur.key]: cur.visible }), {});
  };
  static createDynamicColumnObjFromColumns = columns => {
    return columns.reduce((acc, cur) => ({ ...acc, [cur.key]: cur.type }), {});
  };
  static preparedDynamicDataForExport = (data = [], filterVisibleColumns, forExcel = true, dynamicColumns) => {
    let _data = data.map(record => {
      let item = {};
      for (const key in dynamicColumns) {
        if (dynamicColumns[key] === "date") {
          item[key] = record[key] ? moment(record[key]).format(DATE_FORMAT_DD_MM_YYYY) : "-";
        } else if (typeof record[key] === "number") {
          item[key] = record[key].toString();
        } else {
          item[key] = record[key] || "-";
        }
      }
      if (filterVisibleColumns) {
        Object.keys(item).forEach(itemKey => {
          if (!filterVisibleColumns[itemKey]) {
            delete item[itemKey];
          }
        });
      }
      return item;
    });
    return _data;
  };

  static exportToPDF = params => {
    const {
      type,
      dynamicColumns,
      reportData,
      visibleReportData,
      visibleReportColumns,
      tableDynamicColumns,
      visibility,
      fileName,
      currentDataSource,
      dateHeader,
    } = params;

    // let data;
    let _dynamicColumns = this.createDynamicColumnObjFromColumns(dynamicColumns);
    // if (type === MENU_ITEMS_TYPES.exportAllPDF) {
    //   data = this.preparedDynamicDataForExport(reportData, null, false, _dynamicColumns);
    // } else {
    //   data = this.preparedDynamicDataForExport(visibleReportData, visibility, false, _dynamicColumns);
    // }

    let data =
      type === MENU_ITEMS_TYPES.exportAllPDF
        ? this.preparedDynamicDataForExport(reportData, null, false, _dynamicColumns)
        : type === MENU_ITEMS_TYPES.exportVisiblePDF
        ? this.preparedDynamicDataForExport(visibleReportData, visibility, false, _dynamicColumns)
        : this.preparedDynamicDataForExport(currentDataSource, visibility, false, _dynamicColumns);
    const headers =
      type === MENU_ITEMS_TYPES.exportAllPDF
        ? this.getDynamicHeadersForPDF(tableDynamicColumns)
        : this.getDynamicHeadersForPDF(visibleReportColumns);
    if (fileName === TARGET_DATA_FILE_NAME) {
      FileHandler.exportToPDF(headers, data, { fontSize: 11, padding: 10 }, 250, 200, fileName, 100, dateHeader);
      return;
    }
    if (fileName === RAMP_FILE_NAME) {
      let width = tableDynamicColumns.length * 40;
      FileHandler.exportToPDF(headers, data, { fontSize: 11, padding: 4 }, width, 600, fileName, 80, dateHeader);
      return;
    }
    if (type === MENU_ITEMS_TYPES.exportAllPDF) {
      let width = tableDynamicColumns.length * 40;
      FileHandler.exportToPDF(headers, data, { fontSize: 11, padding: 4 }, width, 1150, fileName);
    } else {
      let width = visibleReportColumns.length * 40;
      FileHandler.exportToPDF(headers, data, { fontSize: 11, padding: 4 }, width, 900, fileName);
    }
  };

  static exportToExcel = params => {
    const { type, dynamicColumns, reportData, visibleReportData, visibility, fileName, currentDataSource, dateHeader } =
      params;

    let _dynamicColumns = this.createDynamicColumnObjFromColumns(dynamicColumns);

    // let data =
    //   type === MENU_ITEMS_TYPES.exportAllExcel
    //     ? this.preparedDynamicDataForExport(reportData, false, true, _dynamicColumns)
    //     : this.preparedDynamicDataForExport(visibleReportData, visibility, true, _dynamicColumns);
    let data =
      type === MENU_ITEMS_TYPES.exportAllExcel
        ? this.preparedDynamicDataForExport(reportData, false, true, _dynamicColumns)
        : type === MENU_ITEMS_TYPES.exportVisibleExcel
        ? this.preparedDynamicDataForExport(visibleReportData, visibility, true, _dynamicColumns)
        : this.preparedDynamicDataForExport(currentDataSource, visibility, true, _dynamicColumns);

    data.unshift(
      this.getDynamicHeadersForExcel([...dynamicColumns], visibility, type === MENU_ITEMS_TYPES.exportAllExcel),
    );

    FileHandler.exportToExcelCustomHeader(data, fileName, dateHeader);
  };

  static preparedDynamicDataForExportNested = (data = [], filterVisibleColumns, forExcel = true, dynamicColumns) => {
    let _data = [];
    data.map(record => {
      let item = {};
      for (const key in dynamicColumns) {
        if (dynamicColumns[key] === "date") {
          item[key] = record[key] ? moment(record[key]).format(DATE_FORMAT_DD_MM_YYYY) : "-";
        } else {
          item[key] = record[key] || "-";
        }
      }
      if (filterVisibleColumns) {
        Object.keys(item).forEach(itemKey => {
          if (!filterVisibleColumns[itemKey]) {
            delete item[itemKey];
          }
        });
      }
      if (record.allocations) {
        record.allocations.forEach(i => {
          for (let key in i) {
            if (key === "currentlyActive" && i[key] === true) {
              item["allocationEndDate"] = CURRENTLY_ACTIVE;
            } else if (key === "allocationStatus") {
              continue;
            } else if (key === "currentlyActive") {
              continue;
            } else {
              item[key] = i[key] || "-";
            }
          }
          if (!i.notes && record["notes"]) {
            item["notes"] = "-";
          }
          _data.push({ ...item });
        });
      } else {
        _data.push(item);
      }
      return item;
    });
    return _data;
  };
  static exportToExcelNested = params => {
    const {
      type,
      dynamicColumns,
      reportData,
      visibleReportData,
      visibility,
      fileName,
      nestedVisibleColumns,
      exportVisibleData,
      nestedDataColumns,
      tableDynamicColumns,
      currentDataSource,
    } = params;
    let visibleData = visibleReportData;
    if (exportVisibleData.length > 0) {
      visibleData = exportVisibleData;
    }

    const _allcolumns = [...dynamicColumns, ...nestedDataColumns];

    let _dynamicColumns = this.createDynamicColumnObjFromColumns(_allcolumns);

    // const data =
    //   type === MENU_ITEMS_TYPES.exportAllExcel
    //     ? this.preparedDynamicDataForExportNested(reportData, false, true, _dynamicColumns)
    //     : this.preparedDynamicDataForExportNested(
    //         visibleData,
    //         {
    //           ...visibility,
    //           ...nestedVisibleColumns,
    //         },
    //         true,
    //         _dynamicColumns,
    //       );
    let data =
      type === MENU_ITEMS_TYPES.exportAllExcel
        ? this.preparedDynamicDataForExportNested(reportData, false, true, _dynamicColumns)
        : type === MENU_ITEMS_TYPES.exportVisibleExcel
        ? this.preparedDynamicDataForExportNested(
            visibleData,
            { ...visibility, ...nestedVisibleColumns },
            true,
            _dynamicColumns,
          )
        : this.preparedDynamicDataForExportNested(
            currentDataSource,
            { ...visibility, ...nestedVisibleColumns },
            true,
            _dynamicColumns,
          );

    data.unshift(
      this.getDynamicHeadersForExcel(
        [..._allcolumns],
        { ...visibility, ...nestedVisibleColumns },
        type === MENU_ITEMS_TYPES.exportAllExcel,
      ),
    );

    FileHandler.exportToExcelCustomHeader(data, fileName);
  };

  static exportToPDFNested = params => {
    const {
      type,
      reportData,
      visibleReportData,
      visibleReportColumns,
      tableDynamicColumns,
      visibility,
      fileName,
      nestedVisibleColumns,
      exportVisibleData,
      nestedDataColumns,
      visibleColumn,
      allData,
      currentDataSource,
    } = params;
    let visibleData = visibleReportData;
    if (exportVisibleData.length > 0) {
      visibleData = exportVisibleData;
    }
    // let data;
    const _allColumns = [...tableDynamicColumns, ...nestedDataColumns];
    let _dynamicColumns = this.createDynamicColumnObjFromColumns(_allColumns);
    // if (type === MENU_ITEMS_TYPES.exportAllPDF) {
    //   data = this.preparedDynamicDataForExportNested(reportData, null, false, _dynamicColumns);
    // } else {
    //   data = this.preparedDynamicDataForExportNested(
    //     visibleData,
    //     {
    //       ...visibility,
    //       ...nestedVisibleColumns,
    //     },
    //     true,
    //     _dynamicColumns,
    //   );
    // }
    let data =
      type === MENU_ITEMS_TYPES.exportAllPDF
        ? this.preparedDynamicDataForExportNested(reportData, null, false, _dynamicColumns)
        : type === MENU_ITEMS_TYPES.exportVisiblePDF
        ? this.preparedDynamicDataForExportNested(
            visibleData,
            {
              ...visibility,
              ...nestedVisibleColumns,
            },
            true,
            _dynamicColumns,
          )
        : this.preparedDynamicDataForExportNested(
            currentDataSource,
            {
              ...visibility,
              ...nestedVisibleColumns,
            },
            true,
            _dynamicColumns,
          );

    const headers =
      type === MENU_ITEMS_TYPES.exportAllPDF
        ? this.getDynamicHeadersForPDF(allData)
        : this.getDynamicHeadersForPDF(visibleColumn);
    if (type === MENU_ITEMS_TYPES.exportAllPDF) {
      let width = allData.length * 40;
      FileHandler.exportToPDF(headers, data, { fontSize: 11, padding: 4 }, width, 1150, fileName);
    } else {
      let width = visibleReportColumns.length * 60;
      FileHandler.exportToPDF(headers, data, { fontSize: 11, padding: 4 }, width, 900, fileName);
    }
  };
  static onMultiSelection = (value, form, fieldName) => {
    if (value === ALL) {
      form.setFieldsValue({ [fieldName]: [ALL] });
    } else if (value === allAgingFilter) {
      form.setFieldsValue({ [fieldName]: [allAgingFilter] });
    } else {
      const selectedValue = [...form.getFieldValue(fieldName)];
      form.setFieldsValue({ [fieldName]: selectedValue.filter(x => x !== ALL && x !== allAgingFilter) });
    }
  };

  static prepareNestedDataForReports = record => {
    let item = {};
    for (const key in record) {
      if (
        typeof record[key] === "string" &&
        isNaN(record[key]) &&
        moment(record[key], moment.ISO_8601, true).isValid()
      ) {
        item[key] = record[key] ? moment(record[key]).format(DATE_FORMAT_DD_MM_YYYY) : " ";
      } else {
        item[key] = record[key];
      }
    }
    if (record.currentlyActive === true || !record.allocationEndDate) {
      item["allocationEndDate"] = CURRENTLY_ACTIVE;
    }
    return item;
  };

  static validateEndDate = (startDate, endDate) => {
    if (
      moment(startDate).format(REPORT_DATE_FORMAT_YYYY_MM_DD) > moment(endDate).format(REPORT_DATE_FORMAT_YYYY_MM_DD)
    ) {
      notification.error({ message: "End Date should be greater than Start Date" });
      return true;
    } else return false;
  };
  static disableDatesTillFeb = currentDate => {
    const feb2023 = moment("2023-02-01");
    const today = moment().endOf("day");

    return currentDate && (currentDate < feb2023 || currentDate > today);
  };
}
export default Utils;
